import axios from 'axios';
import { Message } from 'element-ui';
import store from '@/store';
// import { getCookie } from '@/utils/index'
// import aes from '@/utils/aes'
// axios.defaults.withCredentials = true //让axios携带cookie

const host = `${window.location.protocol}//${window.location.host}`;
// axios 配置
axios.defaults.timeout = 600000;
axios.defaults.baseURL = host;
axios.defaults.headers.post['Content-Type'] = 'application/json';
// 配置跨域请求允许
axios.defaults.headers['Access-Control-Allow-Origin'] = process.env.BASE_API;

// http request 拦截器
axios.interceptors.request.use(
  config => {
    config.withCredentials = true; // 让axios携带cookie
    let userInFo = JSON.parse(sessionStorage.getItem('userInFo'));
    if (userInFo) {
      // config.headers['Authorization'] = userInFo.accesstoken
    }
    return config;
  },
  err => {
    return Promise.reject(err);
  });

// 返回状态判断

axios.interceptors.response.use(
  response => {
    if (response.data.code === '0' || response.data.msg === 'success') {
      return Promise.resolve(response.data);
    } else {
      if (response.data.code === '10001') {
        Message({ type: 'error', message: response.data.msg, duration: 1000 });

        store.commit('DELALL_VISITED_VIEW');
        sessionStorage.clear();
        window.location.href = host + '/#/login';
      } else {
        if (!response.data.msg) {
          return false;
        } else {
          Message({ type: 'error', message: response.data.msg, duration: 1000 });
          return response.data;
        }
      }
    }
  },
  error => {
    if (error.response.status == 401 && (error.response.data.code === '9997' || error.response.data.code === '9996' || error.response.data.code === '9995')) {
      window.location.href = host + '/#/operationLogin';
    } else if (error.response.status == 401 && error.response.data.code === '20000') {
      Message({ type: 'error', message: '对不起，您没有此权限！', title: '温馨提示', duration: 500 });
    } else {
      Message({ type: 'error', message: '服务器开小差了，请联系管理员...', title: '温馨提示', duration: 1000 });
    }
    return Promise.reject(error);
  });

export default axios;
