import Vue from 'vue';

const permiossion = (el, binding) => {
  // 获取权限
  let userInfo = JSON.parse(sessionStorage.getItem('userInFo'));
  if (userInfo) {
    let permissionData = userInfo.lsMenu;
    let bSgin = permissionData.some((item) => {
      return item === binding.value;
    });
    // 如果没有相同内容  隐藏
    if (!bSgin) {
      el.style.display = 'none';
    }
  }
};
Vue.directive('hasPermission', {
  bind(el, binding) {
    permiossion(el, binding);
  },
  update(el, binding) {
    permiossion(el, binding);
  }
});
