/* eslint-disable */
//  获取cookie
const getCookie = function (cname) {
  let arr, reg = new RegExp('(^| )' + cname + '=([^;]*)(;|$)')
  if (arr = document.cookie.match(reg)) {
    return (arr[2])
  } else {
    return null
  }
}
