const prefabrication = {
  state: {
    refreshKey: sessionStorage.getItem('refreshKey') || 0,
    sideArray: JSON.parse(sessionStorage.getItem('sideArray')) || []
  },

  mutations: {
    SET_SIDEARRAY: (state, sideArray) => {
      sessionStorage.setItem('sideArray', JSON.stringify(sideArray));
      state.sideArray = sideArray;
    }
  },
  actions: {

  }
};

export default prefabrication;
